.note {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  width: 100%;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
  border-radius: 7px;
  word-break: break-all;
  background-color: #ffffff;
}

.note__icon {
  margin-top: auto;
  align-self: flex-end;
  cursor: pointer;
  color: #f5ba13;
}
